// Home page styles

body#goals-goals {
	
	h1#site-heading {
		
		font-weight: 			900;
		text-align: 			left;
		
		line-height: 			1.1 !important;



		@include xs {
			font-size: 			$h1-font-size-base-xs;	
		}
		@include sm {
			font-size: 			$h1-font-size-base-sm;	
		}
		@include md {
			font-size: 			$h1-font-size-base-md;	
		}
		@include lg {
			font-size: 			$h1-font-size-base-lg;	
		}
		@include xl {
			font-size: 			$h1-font-size-base-xl;	
		}
		
	}
	
	ul#goals-list {
		max-width: 			600px;
		
		padding: 			0;
		margin: 			0;
		
		list-style: 		none !important;
		
		li {
			list-style: 	none;
			padding: 		0;
						
			box-sizing: 	border-box;
	
			progress[value] {
				
				position: relative;
				display: block;

				-webkit-appearance: none;
						appearance: none;
						
				width: 		100%;
				
				padding: 	0;
				margin: 	20px 0;

				&.standard {
				
					height: 	20px;

					&::-webkit-progress-bar {
						background-color: 	$gray-200;
						@include dark-mode {
							background: 	$gray-700;
						}
						border: 			none;
						border-radius: 		2px;
					}
					
					&::-webkit-progress-value {
						background-color: 	$cyan;			
						border-radius: 2px; 
						background-size: 600px;
					}

				}


				&.stretch-top {

					height: 10px;
					margin-bottom: 0;

					&::-webkit-progress-bar {
						background-color: 	$gray-200;
						@include dark-mode {
							background: 	$gray-700;
						}
						border: 			none;			
						border-top-left-radius: 2px; 
						border-top-right-radius: 2px;			
						border-bottom-left-radius: 0; 
						border-bottom-right-radius: 0;
					}
					
					&::-webkit-progress-value {
						background-color: 	$cyan;			
						border-top-left-radius: 2px; 
						border-top-right-radius: 2px; 		
						border-bottom-left-radius: 0; 
						border-bottom-right-radius: 0;
						background-size: 600px;
					}

				}
				&.stretch-bottom {

					height: 10px;
					margin-top: 0;

					&::-webkit-progress-bar {
						background-color: 	$cyan;
						@include dark-mode {
							background: 	$cyan;
						}
						border: 			none;			
						border-top-left-radius: 0; 
						border-top-right-radius: 0; 	
						border-bottom-left-radius: 2px; 
						border-bottom-right-radius: 2px;
					}
					
					&::-webkit-progress-value {
						background-color: 	$purple;		
						border-top-left-radius: 0; 
						border-top-right-radius: 0; 		
						border-bottom-left-radius: 2px; 
						border-bottom-right-radius: 2px; 
						background-size: 600px;
					}

				}
				
			}
			
			&.totaliser {
				
				background: $blockquote-background;
				
				@include dark-mode {
					background: $blockquote-background-dark-mode;
				}
				
				padding: 		20px;
				margin-top: 	60px;
				
				border-radius: 	5px;
				
				box-sizing: border-box;
				
				h2 {
					margin-top: 0;
					margin-bottom: 0;
					padding-top: 0;
				}
				
				progress[value] {
					
					&::-webkit-progress-bar {
						background-color: 	$gray-500;
					}
				}
				
			}

			
		}
	}
		
}